<template>

    <div id="app">
        <shead :headid="$route.query.headid"></shead>

        <div class="wrap">


            <div class="filter-box">

                <div class="item">

                    <div class="title">{{lanmu.name}}:</div>
                    <div class="content-list">
                        <ul>

                            <li>
                                <span class="subtype" :class="{active:index==selectedIndex}" v-if="lanmu.subtypes!=null" @click="getShangpin(lanmu.id,subtype.id,index)" v-for="(subtype,index) in lanmu.subtypes">{{subtype.name}}</span>
                            </li>


                        </ul>
                    </div>
                </div>

            </div>

            <div class="two-split">
                <div style="width:960px;" class="split-left">

                    <div v-if="listShangpin.length>0" class="picture-list">

                        <ul>

                            <li v-for="item in listShangpin">

                                <div class="item">


                                    <div class="img">
                                        <router-link :to="`/e/shangpininfo?headid=${$route.query.headid}&id=${item.id}`" >
                                            <img :src="hostHead+item.images[0]" />
                                        </router-link>
                                    </div>

                                    <div class="name">{{item.name}}</div>
                                    <div class="price">{{item.hyjia}}元 </div>

                                </div>
                            </li>

                        </ul>

                    </div>

                    <div v-else class="no-record-tip">
                        <div class="content">
                            <i class="fa fa-warning"></i>没有找到相关信息
                        </div>

                    </div>





                    <div class="clear"></div>

                    <el-pagination
                        background
                        @size-change="pagesizeChange"
                        @current-change="pageindexChange"
                        :current-page="pageindex"
                        :page-sizes="[pagesize]"
                        :page-size="pagesize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="total">
                    </el-pagination>

                </div>
                <div style="width:250px;" class="split-right">
                    <div  class="vm-sidebar">
                        <div class="hd">热销商品</div>
                        <div class="bd">

                            <div v-for="(shangpin,index) in hotSales " class="item">
                                <router-link :to="'/e/shangpininfo?id='+shangpin.id">
                                    <div class="image-wrap">
                                        <img :src="hostHead+shangpin.images[0]" />
                                    </div>
                                    <div class="tag">{{index+1}}</div>
                                    <div class="text-wrap">
                                        <div class="name">{{shangpin.name}}</div>
                                        <div class="muted">销售:{{shangpin.saledcount}}{{shangpin.danwei}}</div>
                                        <div class="bm-wrap">¥{{shangpin.hyjia}}</div>
                                    </div>
                                </router-link>
                            </div>

                        </div>
                    </div>
                </div>
            </div>

            <div class="fn-clear"></div>
        </div>
        <bottom></bottom>
    </div>
</template>

<script>
    import Bottom from "./bottom";
    import Shead from "./shead";
    export default {
        name: "shangpinlist",
        data(){
            return {
                hotSales: [],
                hostHead: this.$app.contextPath,
                listShangpin: [],
                pageindex: 1,
                typeid: this.$route.query.typeid,
                lanmu: {},
                selectedIndex: 0,
                pagesize: 10,
                total: 10,
            }
        },
        async created(){
            await this.getLanmu();
            await this.getShangpin(this.typeid);
            await this.getHotSales();
        },
        components: {Bottom, Shead},
        methods:{


            pagesizeChange: function (pagesize) {
                this.pagesize = pagesize;
            },
            pageindexChange: function(pageindex){
                this.pageindex = pageindex;
                console.log(this.pageindex);
                this.getShangpin(this.typeid);
            },
           async getShangpin(typeid,subtypeid,index) {
                this.selectedIndex=index;
                let param={
                    currentpageindex:this.pageindex,
                    pagesize:this.pagesize
                };
                if(typeid!="")
                    param.typeid=typeid;
                if(subtypeid!=null)
                    param.subtypeid=subtypeid;

                let url = "admin/shangpin/list";
                this.$http.post(url,param).then(res => {
                    console.log(res.data);
                    if(res.data!=null&&res.data.data!=null) {
                        let pageInfo=res.data.data;
                        this.total=pageInfo.total;
                        this.listShangpin = pageInfo.list;
                    }
                });
            },

            async getLanmu(){
                let url = "admin/lanmu/load";
                this.$http.post(url,{
                    id:this.typeid
                }).then(res => {
                    console.log(res.data);
                    if(res.data!=null&&res.data.data!=null) {
                        this.lanmu =res.data.data;
                    }
                });
            },

            getHotSales(){
                let url="admin/shangpin/hotsales";
                this.$http.post(url).then(res=>{
                    if(res.data.data!=null) {
                        res.data.data.forEach(c => {
                            c.images=c.tupian.split("$;");
                        });
                        this.hotSales = res.data.data;
                    }
                });
            }
        }

    }
</script>

<style scoped>

</style>
